.frame::before,
.frame::after {
	content: '';
	position: absolute;
	width: 6px;
	height: 483px;
	border-radius: 3px;
	/*background: linear-gradient(to bottom, #049, #fff, #049);*/
}
.frame::before {
	top: 0;
	left: 0;
	/*background: linear-gradient(to bottom, transparent, #049, #fff 98%, transparent);*/
	background: linear-gradient(to bottom, transparent, #049, #fff 95%, transparent);
}
.frame::after {
	bottom: 0;
	right: 0;
	/*background: linear-gradient(to bottom, transparent, #fff 2%, #049, transparent);*/
	background: linear-gradient(to top, transparent, #049, #fff 95%, transparent);
}
[frame-type='diamond']::before {
	background: linear-gradient(to bottom, transparent, #049, #fff 95%, transparent);
	/*background: linear-gradient(to bottom, transparent, #049, #fff 85%, transparent 90%, #fff 95%, transparent);*/
}
[frame-type='club']::before {
	background: linear-gradient(to bottom, transparent, #049, #fff 85%, transparent 90%, #fff 95%, transparent);
	/*background: linear-gradient(to bottom, transparent, #049, #fff 75%, transparent 80%, #fff 85%, transparent 90%, #fff 95%, transparent);*/
}
[frame-type='heart']::before {
	background: linear-gradient(to bottom, transparent, #049, #fff 75%, transparent 80%, #fff 85%, transparent 90%, #fff 95%, transparent);
	/*background: linear-gradient(to bottom, transparent, #049, #fff 65%, transparent 70%, #fff 75%, transparent 80%, #fff 85%, transparent 90%, #fff 95%, transparent);*/
}
[frame-type='spade']::before {
	background: linear-gradient(to bottom, transparent, #049, #fff 65%, transparent 70%, #fff 75%, transparent 80%, #fff 85%, transparent 90%, #fff 95%, transparent);
	/*background: linear-gradient(to bottom, transparent, #049, #fff 55%, transparent 60%, #fff 65%, transparent 70%, #fff 75%, transparent 80%, #fff 85%, transparent 90%, #fff 95%, transparent);*/
}
[frame-type='diamond']::after {
	background: linear-gradient(to top, transparent, #049, #fff 95%, transparent);
	/*background: linear-gradient(to top, transparent, #049, #fff 85%, transparent 90%, #fff 95%, transparent);*/
}
[frame-type='club']::after {
	background: linear-gradient(to top, transparent, #049, #fff 85%, transparent 90%, #fff 95%, transparent);
	/*background: linear-gradient(to top, transparent, #049, #fff 75%, transparent 80%, #fff 85%, transparent 90%, #fff 95%, transparent);*/
}
[frame-type='heart']::after {
	background: linear-gradient(to top, transparent, #049, #fff 75%, transparent 80%, #fff 85%, transparent 90%, #fff 95%, transparent);
	/*background: linear-gradient(to top, transparent, #049, #fff 65%, transparent 70%, #fff 75%, transparent 80%, #fff 85%, transparent 90%, #fff 95%, transparent);*/
}
[frame-type='spade']::after {
	background: linear-gradient(to top, transparent, #049, #fff 65%, transparent 70%, #fff 75%, transparent 80%, #fff 85%, transparent 90%, #fff 95%, transparent);
	/*background: linear-gradient(to top, transparent, #049, #fff 55%, transparent 60%, #fff 65%, transparent 70%, #fff 75%, transparent 80%, #fff 85%, transparent 90%, #fff 95%, transparent);*/
