.circle {
	width: var(--halfW);
	height: var(--halfW);
	border-radius: 50%;
}
.circle::before {
	content: '';
	position: absolute;
	top: 6px;
	left: 6px;
	width: 324px;
	height: 324px;

	background: #013;
	border-radius: 50%;
	overflow: hidden;
}
.circle--angle,
.circle--angle::before {
	border-radius: 0 50% 50% 50%;
}



