.skill {
	position: absolute;
	z-index: 2;
	top: 50px;
	bottom: 50px;
	left: 50px;
	right: 50px;
	font-size: 40px;
	line-height: 1;
	text-align: right;
	color: rgba(255, 255, 255, .4);
	pointer-events: none;
}
.skill::before,
.skill::after {
	position: absolute;
	left: 0;
	right: 0;
}
.skill::after {
	font-size: 30px;
	line-height: 1.5;
	text-align: left;
	color: rgba(255, 255, 255, .2);
}

